body {
  font-family: 'Work Sans', 'Helvetica Neue', Arial, sans-serif;
}

p {
  letter-spacing: -0.03em;
}

.navbar-brand {
  font-family: 'Shadows Into Light';
  font-size: 40px;
  margin: 0 0 20px;
}

.hero {
  height: 100%;
  width: 100%;
  min-height: 400px;
  background: url('./static/img/detroit-skyline.jpg') center center no-repeat;
  background-size: cover;
  /*box-shadow: inset 0 0 0 1000px rgba(0, 48, 127, 0.5);*/
}

.hero h1 {
  font-size: 72px;
  padding: 10px 40px;
  background: rgba(255, 255, 255, 0.6);
  color: #000;
  font-weight: 700;
  line-height: 1.3em;
}

.hero-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.hero-body .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Intro {
  padding: 40px 172px;
  text-align: center;
}

.Intro h2 {
  font-weight: 100;
}

.Logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Logo a {
  border: 1px solid #ccc;
  padding: 12px;
}
.Logo .location {
  -ms-flex-item-align: end;
      align-self: flex-end;
  font-size: 14px;
  font-style: italic;
}

.logos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 12px;
}



